
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 20px;
  font-family: 'Open Sans', sans-serif, "宋体";
}
body {
  background-image: url('../static/imgs/bg.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
}
h1{
  cursor: pointer;
}
.clear {
  clear: both;
}

/* 音乐控件 */
.music-fly {
  position: absolute;
  left: 0;
  top: 0;
}
.music-fly:hover {
  cursor: pointer;
}

/* 头部开始 */
.header {
  color: white;
}
.logoname {
  padding-bottom: 10px;
  display: inline-block;
  font-family: 'Open Sans', sans-serif, "宋体";
  font-size: 3rem;
  text-align: center;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 120%;
  background-image: -webkit-linear-gradient(left, #df21f5, skyblue 25%, orange 50%, #000a80 75%, #df21f5);
  -webkit-animation: content-animation 4s infinite linear;
          animation: content-animation 4s infinite linear;
}
@-webkit-keyframes content-animation {
0% {
    background-position: 0, 0
}
100% {
    background-position: -100% 0;
}
}
@keyframes content-animation {
0% {
    background-position: 0, 0
}
100% {
    background-position: -100% 0;
}
}
.header ul {
  display: inline-block;
  list-style: none;
  padding-left: 0;
  float: right;
}
.header ul li {
  color: white;
  text-align: center;
}
.header ul li a {
  color: white;
  font-size: 14px;
  text-decoration: none;
}
.header ul li a:hover {
  color: #23D6E7;
}
.nav>li>a:focus, .nav>li>a:hover{
  background: none;
}
/* 媒体查询，检测小屛 */
@media (max-width: 493px) {
.logoname {
    width: 100%;
    text-align: center;
    font-size: 2rem;
}
.header ul {
    width: 100%;
}
.header ul li {
    padding-top: 0.5rem;
}
}

/* 媒体查询，检测手机 */
@media (max-width: 384px) {
.logoname {
    width: 100%;
    text-align: center;
    font-size: 2rem;
}
.header ul {
    width: 100%;
    padding-left: 0;
}
.header ul li {
    width: 100%;
    padding-top: 0rem;
    text-align: center;
    padding: 0.3rem;
}
.header ul li a {
    color: white;
}
.header ul li a {
    color: #fff;
    text-decoration: none;
}
.header ul li:hover {
    background-color: #23D6E7;
}
}
.router-link-active {
  color: skyblue !important;
}

/* 头部结束 */
